<template>
  <view class="list_item" @click="toDetail">
    <view class="left">
      <image class="image" :src="showImageUrl" mode="aspectFill" />
    </view>
    <view class="right">
      <view class="row1 flex_between">
        <view class="title">{{ info.name }}</view>
      </view>
      <view class="row2" v-if="info.sport_tag_str">
        <paTag
          v-for="(sport_item, sindex) in info.sport_tag_str.slice(0, 2)"
          :key="sindex"
          style="margin-right: 10rpx"
        >
          {{ sport_item }}
        </paTag>
        <paTag v-if="info.sport_tag_str.length > 2">...</paTag>
      </view>
      <view class="row3 flex_between">
        <uni-icons
          type="iconfont-location-outline"
          custom-prefix="iconfont"
          size="16"
          color="#A5ADBA"
        ></uni-icons>
        <view class="distance" v-if="info.show_distance">{{ info.show_distance }}</view>
        <view class="address">{{ info.address || '暂无位置信息' }}</view>
        <view
          class="foo-right"
          v-if="
            info.service_status == 1 &&
            info.service_provider != 0 &&
            info.service_type.includes('stadium_order')
          "
          @click.prevent="handleReserve"
        >
          预订
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import defaultUrl from '@/static/images/default-stadium.png'
export default {
  name: 'stadiumItem',
  emits: ['reserve', 'click'],
  props: {
    info: {
      type: Object,
      default: () => {
        return {}
      },
    },
  },
  data() {
    return {}
  },
  computed: {
    toDistance() {
      let item = this.info
      let distance = '位置未知'
      return distance
    },
    showImageUrl() {
      return this.info.images_url[0] ? this.info.images_url[0] : defaultUrl
    },
  },
  methods: {
    handleReserve() {
      this.$emit('reserve', this.info)
    },
    toDetail() {
      this.$emit('click', this.info)
    },
  },
}
</script>

